/* CSS Document */
body,p,ul,ol,dl,dd,h1,h2,h3,h4,h5,h6,td,input,select,option,textarea{
    margin:0;
    padding:0;
}
html,body{
    height: 100%;
}
html{
    font-size: 26.67vw;
}
body{
    font-family:"\5FAE\8F6F\96C5\9ED1",Arial;
    display:-webkit-flex;
    background:#efefef;
}
a{
    text-decoration:none;
}
ul,ol{
    list-style:none;
}
img{
    border:none;
    display: block;
}
.BigBox{
    width:3.75rem;
    height:100%;
    display:-webkit-box;
    display:-ms-flexbox;
    display:flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
}

 

.heads{
    background:white;
    width:100%;
    height:.43rem;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    border-bottom:1px solid #e6e6e6;
}
.heads img{
    width:.25rem;
    height:.22rem;
    margin-left: .18rem;
    margin-right: 10px;
}
.search{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width:2.64rem;
    height:.27rem;
    background:#eeeeee;
    border-radius: 3px;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.search img{
    width:.16rem;
    height:.18rem;
}
.search span{
    font-size:12px;
    color:#979797;
    display: block;
}
 

.box{
   width:100%;
    -webkit-box-flex:1;
        -ms-flex:1;
            flex:1;
    overflow-y: auto;
    display:-webkit-box;
    display:-ms-flexbox;
    display:flex;
   -webkit-box-orient: vertical;
   -webkit-box-direction: normal;
       -ms-flex-direction: column;
           flex-direction: column;
}

 
